/* eslint-disable react/destructuring-assignment */
/* eslint-disable react/display-name */
/* eslint-disable no-nested-ternary */
/* eslint-disable react/no-array-index-key */
/* eslint-disable react/jsx-key */
import React, { useEffect, useState, useCallback } from "react";
import axios from "axios";
import { getUser } from "../../../helpers/Utils";
import { Link } from "react-router-dom";
import jupyterLabWorkspace from "../../../assets/jupyterLabWorkspace.png";
import jupyterNotebookWorkspace from "../../../assets/jupyterNotebookWorkspace.png";
import vscodeWorkspace from "../../../assets/codeWorkspace.png";
import laptop from "../../../assets/workspaceImg1.png";

// NAMES FOR CUSTOM KATONIC IMAGES 
const imageName = {
  "katonic/katonic-studio:3.0": "Katonic Studio",
  "katonic/notebook-env:base": "Katonic-base",
  "katonic/notebook-env:pyspark": "PySpark",
  "katonic/notebook-env:torchcpu1.8.1": "PyTorch",
  "katonic/notebook-env:tf2.4": "Tensorflow-2",
  "katonic/notebook-env:tf1.4": "Tensorflow-1",
  "katonic/notebook-env:scikt-learn": "Scikt-learn",
  "katonic/vscode:latest": "VS Code",
  "katonic/vscode:miniconda": "VS Code (python)",
  "katonic/lab-env:torchcpu1.8.1": "PyTorch",
  "katonic/lab-env:tf2.4": "Tensorflow-2",
  "katonic/lab-env:tf1.4": "Tensorflow-1",
  "katonic/lab-env:scikt-learn": "Scikt-learn",
};

// VALIDATION FOR IMAGE NAME 
const getImageName = (propertyName) => {
  return imageName[propertyName] ? imageName[propertyName] : propertyName;
};

// DELETE NOTEBOOK API CALL
const deleteNotebook = async (name) => {
  await axios.get(`/platform/api/notebook/${name}/delete`);
};

// START NOTEBOOK API CALL
const startNotebook = async (name) => {
  await axios.get(`/platform/api/notebook/${name}/startNb`);
};

// STOP NOTEBOOK API CALL
const stopNotebook = async (name) => {
  await axios.get(`/platform/api/notebook/${name}/stopNb`);
};

const images = {
  vsCodeImgs: ["katonic/vscode:latest", "katonic/vscode:miniconda"],
  jupyterLabImages: [
    "katonic/katonic-studio:3.0",
    "katonic/notebook-env:base",
    "katonic/lab-env:torchcpu1.8.1",
    "katonic/lab-env:tf2.4",
    "katonic/lab-env:tf1.4",
    "katonic/lab-env:scikt-learn",
  ],
  jupyterNotebookImages: [
    "katonic/notebook-env:torchcpu1.8.1",
    "katonic/notebook-env:tf2.4",
    "katonic/notebook-env:tf1.4",
    "katonic/notebook-env:scikt-learn"
  ]
}

// DELETE POPUP COMPONENT
const Popup = (props) => {
  return (
    <div className="popup-box" style={{zIndex: 1000}} key={props.workspace}>
      <div className="box">
        <div className="popup-box-content">
          <div className="popup-info">
            <div className="popup-content-left">
              <i className="simple-icon-exclamation deleteIcon popup-icon" />
            </div>
            <div className="popup-content-right">
              <h3 style={{ marginTop: "3rem" }}>
                You are about to delete a workspace!
              </h3>
              <h3>
                This will delete your workspace{" "}
                <strong style={{ color: "#5c31ff" }}>
                  {props.workspace}
                </strong>{" "}
                permanently
              </h3>
              <h3>Are you sure?</h3>
            </div>
          </div>

          <div className="popup-box-btns">
            <button className="popup-btn" onClick={props.handleClose}>
              Cancel
            </button>
            <button
              className="popup-btn popup-btn-delete"
              onClick={() => {
                deleteNotebook(props.workspace);
                setTimeout(() => window.location.reload(), 2000);
              }}
            >
              Delete
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

// INTRO PAGE COMPONENT WHEN THERE ARE NO NOTEBOOKS
const IntroPage = () => {
  return (
    <div className="introPage">
      <h3>Get Started with Workspaces</h3>
      <img className="workspaceImg" src={laptop} alt="intro" />
      <p>
        Workspace sessions are Katonic hosted Interactive Development
        Environment (IDE) sessions where you can create notebooks with
        Jupyter, R Studio etc. &nbsp;
        <span>
          <a
            href="https://learn.katonic.ai/"
            target="_blank"
            rel="noreferrer"
          >
            Learn more
          </a>
        </span>
      </p>
    </div>
  );
};

// CREATE WORKSPACE BUTTON COMPONENT
const CreateWorkspaceButton = () => {
  return (
    <Link to="/platform/workspace/createWorkspace">
      <button className="workspaceCreateBtn">
        <span>+</span>
        Create Workspace
      </button>
    </Link>
  );
};

// START, STOP AND CONNECT BUTTON COMPONENTS 
const renderDownButtons = (value1, value2, name, image, username) => {
  if (value1 === 1) {
    return (
      <div>
        <button className="runningBtn connectBtn">
          {" "}
          <a
            style={{ fontSize: "12px", color: "white" }}
            href={ images.vsCodeImgs.includes(image) ? `/${name}-${username}/vscode/` : images.jupyterNotebookImages.includes(image) ? `/${name}-${username}/` : `/${name}-${username}/lab?`}
            target="_blank"
            rel="noreferrer"
          >
            Connect
          </a>
        </button>
        <button
          onClick={() => {
            stopNotebook(name);
            setTimeout(() => window.location.reload(), 2000);
          }}
          className="runningBtn stopBtn"
        >
          Stop
        </button>
      </div>
    );
  } else if (value2 >= 1) {
    return (
      <button className="runningBtn connectBtn" disabled>
        Start
      </button>
    );
  } else {
    return (
      <button
        onClick={() => {
          startNotebook(name);
          setTimeout(() => window.location.reload(), 2000);
        }}
        className="runningBtn connectBtn"
      >
        Start
      </button>
    );
  }
};

// CPU DISPLAY COMPONENT
const displayCPU = (value) => {
  if (value.charAt(value.length - 1) === "m") {
    value = value.slice(0, -1);
    value = parseInt(value);
    value = value / 1000;
    value = value.toString();
    return value;
  } else {
    return value.concat(".0");
  }
};

// MEMORY DISPLAY COMPONENT
const getMemory = (value) => {
  value = value.slice(0, -1);
  value = value.concat("B");
  return value;
};

// COMPONENT TO SHOW WORKSPACE STATUS
const renderButton = (value1, value2) => {
  if (value1 === 1) {
    return <button className="runningBtn">Running</button>;
  } else if (value2 >= 1) {
    return <button className="processingBtn">Processing</button>;
  } else {
    return <button className="stoppedBtn">Stopped</button>;
  }
};

// WORKSPACE AGE CONVERSION
const renderAge = (value) => {
  let createTime = new Date(value);
  createTime = Math.abs(Date.now() - createTime) / (1000 * 3600 * 24);
  Math.floor(createTime);
  if (Math.floor(createTime) === 1) {
    return `${Math.floor(createTime)} day`;
  }
  return `${Math.floor(createTime)} days`;
};

// NOTEBOOK TABLE COMPONENT
const NotebookTable = () => {
  const [error, setError] = useState(null);
  const [notebooks, setNotebooks] = useState([]);
  const [isLoaded, setIsLoaded] = useState(false);
  const [username, setUsername] = useState('');

  const [openSts, setOpenSts] = useState(false);

  const togglePopup = useCallback((stsName = false) => {
    setOpenSts(stsName);
  }, [setOpenSts]);

  const getAllNotebooks = useCallback(async () => {
    setIsLoaded(false);
    await fetch(`/platform/api/notebook`)
      .then(res => res.json())
      .then(workspaces => {
        setIsLoaded(true);
        setNotebooks(workspaces); 
      },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )
  }, [setIsLoaded, setNotebooks, setError]);

  useEffect(() => {
    getUser().then((user) => {
      setUsername(user.userName);
    });
    getAllNotebooks();
  }, [getAllNotebooks]);

  const WorkspaceHeader = () => (
    <div className="notebookHead">
      <p style={{ fontSize: "22px", fontWeight: "500" }}>Workspaces</p>
      <div className="notebookHeadRight">
        <CreateWorkspaceButton />
        <button
          className="refreshButton"
          type="button"
          onClick={getAllNotebooks}
        >
          Refresh
        </button>
      </div>
    </div>
  )

  return (
    <>
      <div className="h-100 table-container">
        <WorkspaceHeader />
        {
          !isLoaded && <div className="loading" />
        }
        {
          isLoaded && notebooks.length === 0 && <IntroPage />
        }
        {
          notebooks.map((item) => (
              <div className="newTable" key={item.stsName}>
                <div id="tableLeft">
                  <div className="imgContainer">
                    {
                      item.stsImageName === ""
                    }
                    <img src={images.vsCodeImgs.includes(item.stsImageName) ? vscodeWorkspace : images.jupyterNotebookImages.includes(item.stsImageName) ? jupyterNotebookWorkspace : jupyterLabWorkspace} alt="Workspace Icon" />
                  </div>
                  {renderDownButtons(
                    item.stsReadyReplicas,
                    item.stsReplicas,
                    item.stsName,
                    item.stsImageName,
                    username
                  )}
                </div>
                <div id="tableRight">
                  <div className="rightTop">
                    <div className="innerSection">
                      <p className="dataLabel">Name</p>
                      <p className="dataBox">{item.stsName}</p>
                      <p className="dataLabel">Age</p>
                      <p className="dataBox">{renderAge(item.stsCreateTime)}</p>
                    </div>
                    <div className="innerSection">
                      <p className="dataLabel">Image</p>
                      <p className="dataBox">
                        {getImageName(item.stsImageName)}
                      </p>
                      <p className="dataLabel">Memory</p>
                      <p className="dataBox">{getMemory(item.stsMemory)}</p>
                    </div>
                    <div className="innerSection">
                      <div className="btnContainer statusIndicator">
                        {renderButton(item.stsReadyReplicas, item.stsReplicas)}
                      </div>
                      <p className="dataLabel">CPU</p>
                      <p className="dataBox">{displayCPU(item.stsCpu)}</p>
                    </div>
                  </div>
                  <div className="rightBottom">
                    <div className="btnContainer deleteBtnContainer">
                      <button
                        style={{
                          border: "none",
                          backgroundColor: "transparent",
                        }}
                        onClick={() => togglePopup(item.stsName)}
                        type="button"
                      >
                        <i className="simple-icon-trash deleteIcon" />
                      </button>
                      {openSts && openSts === item.stsName && (
                        <Popup
                          workspace={item.stsName}
                          handleClose={togglePopup}
                        />
                      )}
                    </div>
                  </div>
                </div>
              </div>
            )
          )
        }
      </div>
    </>
  );
};

export default NotebookTable;